footer {
  position: relative;
  top: 60px;
  background-color: white;
}

footer .footer_container {
  color: green;
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-evenly;
  align-items: flex-start;
  position: relative;
  top: 30px;
}

footer .footer_container .footr_details_one h3 {
  font-size: 16px;
  text-align: left;
}

footer .footer_container .footr_details_one p {
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 12px;
  letter-spacing: 0.5px;
  text-align: left;
  cursor: pointer;
}

footer .lastdetails {
  background-color: #eaeaea;
  height: 5vh;
  margin: auto;
}

footer img {
  display: block;
  width: 100px;
  text-align: center;
  margin-bottom: 20px;
  cursor: pointer;
}

footer p {
  color: black;
  font-size: 12px;
  letter-spacing: 0.3px;
  text-align: center;
  margin-top: 20px;
}

.lastdetails > p {
  padding: 5px;
}

.icon-wrapper {
  display: flex;
}
.icon-wrapper .icon {
  font-size: 30px;
  color: white;
  margin-top: 7px;
}
.footr_details_one #facbook {
  background-color: #3b5998;
  width: 45px;
  height: 45px;
  margin: 0px 8px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
}
.footr_details_one #pinterest {
  background-color: #db3236;
  width: 45px;
  height: 45px;
  margin: 0px 8px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
}
.footr_details_one #twitter {
  background-color: #00aced;
  width: 45px;
  height: 45px;
  margin: 0px 8px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
}
.footr_details_one #instagram {
  background-color: #517fa4;
  width: 45px;
  height: 45px;
  margin: 0px 8px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;

}

@media (max-width: 700px) {
  .footer_container .forres {
    display: none;
  }
}
@media (max-width: 500px) {
  .footer_container .forres {
    display: none;
  }
}
